.contactContainer{
    width:70vw;
    max-width: 550px;
    /* display:flex;
    flex-direction: column; */
    margin:2rem auto 5rem;
    padding:1rem 2rem;
    /* align-items: center; */
    box-shadow: 0 0 0.5rem #a1a1a1;
    background-color: #eeeded;
    border-radius: 0.2rem;

}
/* .nameContainer{
   display:flex;

   justify-content: space-between;
}
.nameContainer>input{
    width:49%;
