.projectContainer,.projectDetailContainer{
    display:flex;
    flex-wrap:wrap;
    justify-content:space-around;
    width:80vw;
    margin:2rem auto 5rem auto;
}
.projectCard {
    margin:1rem;
    width:400px;
    min-height:300px;
    text-align: center;
    border-radius:0.2rem;
    padding:1rem;
    box-shadow: 0.5rem 0.5rem 0.5rem gray;
    transition:transform 0.3s ease-in-out;
    position:relative;
}
.projectCard a{
    text-decoration: none;
}
.projectName{
    font-size: 1.5rem;
    font-weight:400;
    background-color: #515151;
    color:#eeeded;
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    border-bottom-left-radius:0.2rem;
    border-bottom-right-radius:0.2rem;

    /* background-color: #000000; */
    padding:0.5rem;
    box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.74);
    border-top:2px solid #fec697;

    /* border-radius:0.3rem;
    border-top:2px solid #fec697;
    border-left:2px solid #fec697;
    border-bottom:2px solid #895226;
    border-right:2px solid #895226;
    box-shadow: 0.5rem 0.5rem 1rem rgb(24, 24, 24);
    padding:1rem; */
    transition:transform 0.3s ease-in-out;
}
/* .deployLink,.codeLink{
    display:none;
} */
.projectCard:hover{
    transform: scale(1.05);
    background-color: #ffffff;
}
.projectCard:hover h2,.projectCard:hover img{
    /* box-shadow: 0rem 0rem 2rem white; */
}
