.projectCardInfo{
    width: 100%;
    height:290px;
    display:flex;
    justify-content: center;
    align-items:flex-end;
    border-radius: 0.2rem;
    /* position: relative; */
}
.projectCard:hover .projectCardInfo{
    align-items: flex-end;
}
.projectLinkContainer{
    width:100%;
