.projectPicture{
    width:25%;
    height:25%;
    transition:transform 0.3s ease-in-out;

}
.projectPicture:hover{
    transform:scale(1.1)
}
.large{
    width:80%;
    height:80%;
    position:absolute;
    top:10%;
    left:10%;
}
