    background-image: url(/public/images/projectFiveMain.png);
}
.projectSix::after{
    background-image: url(/public/images/projectSixMain.png);
}
.projectOne::after,.projectTwo::after,.projectThree::after,.projectFour::after,.projectFive::after,.projectSix::after {
    content:"";
    position:absolute;
    background-size: cover;
    top:0;
    left:0;
    right:0;
    bottom:0;
    z-index: -1;
    background-position: center;
    border-radius: 0.2rem;

}
.projectOne:hover::after,.projectTwo:hover::after,.projectThree:hover::after,.projectFour:hover::after,.projectFive:hover::after,.projectSix:hover::after {
    opacity:0.8;

}
.projectDeployed{
    padding:0.5rem 1rem;
    font-size: 1rem;
    font-weight:400;
    background-color: #3f3f3f;
    color:#eeeded;
    border-radius:0.3rem;
    border-top:2px solid #fec697;
    border-left:2px solid #fec697;
    border-bottom:2px solid #895226;
    border-right:2px solid #895226;
    box-shadow: 0.5rem 0.5rem 1rem rgb(24, 24, 24);
    transition:tranform 0.3s ease-in-out;
    position:relative;
}
.projectDeployed:hover::after{
    content:"Deployed";
    position: absolute;
    top:-80%;
    left:50%;
    background-color: #000000;
    border-radius:0.1rem;
    padding:0.2rem;
    opacity: 0.7;
    font-size: 0.9rem;
    transform:translateX(-50%);
}
.projectCode{
    width:2.5rem;
    background-color: #3f3f3f;
    color:#eeeded;
    border-radius:0.3rem;
    border-top:2px solid #fec697;
    border-left:2px solid #fec697;
    border-bottom:2px solid #895226;
    border-right:2px solid #895226;
    box-shadow: 0.5rem 0.5rem 1rem rgb(24, 24, 24);
    padding:0.1rem;
    transition:transform 0.3s ease-in-out;
    /* background-color: black; */
}
.codeLink{
    position:relative;
    height:100%;
}
.codeLink:hover::after{
    content:"Source Code";
    color:white;
    width:6rem;
    position: absolute;
    top:-240%;
    left:-1.9rem;
    background-color: #000000;
    border-radius:0.1rem;
    padding:0.2rem;
    opacity: 0.7;
    font-size: 0.9rem;
}

@media screen and (max-width:450px) {
    .projectCard{
        margin:2rem 0;
        width:100vw;
    }
}